.axis path,
.axis line {
  stroke: #e0e0e0;
}

.axis text {
  font-size: 11px;
  fill: black;
}

.axis-Bottom text {
  -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
  text-anchor: end;
}


